* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "PT Sans", "Arial", sans-serif;
}
@font-face {
  font-family: "PT Sans";
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-weight: bold;
  font-style: normal;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}
.body {
  margin: 0;
  padding: 0;
  font-family: "PT Sans", "Arial", sans-serif;
  min-height: 100vh;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content 1fr min-content;
}
.body--scrolloff {
  overflow: hidden;
}
.header {
  width: 100%;
  height: 120px;
  background-color: #DCDCDC;
}
.main {
  width: 100%;
  height: 100%;
}
.footer {
  width: 100%;
  height: 120px;
  background-color: #DCDCDC;
}
.slider {
  width: 100%;
  height: 400px;
  padding: 40px 20px;
  display: grid;
  grid-gap: 40px;
  justify-items: center;
  grid-template-columns: 1fr;
  grid-template-rows: min-content min-content;
  background-color: #F5F5F5;
}
@media (max-width: 600px) {
  .slider {
    width: 100%;
    padding: 40px 0;
  }
}
.sleder-controls {
  height: min-content;
  position: relative;
  width: min-content;
  display: grid;
  grid-template-columns: repeat(4, min-content);
  align-items: center;
  grid-column-gap: 20px;
}
.sleder-controls::before {
  content: '';
  position: absolute;
  width: 4px;
  height: 100%;
  right: 50%;
  transform: translateX(100%) rotate(15deg);
  top: 0;
  background-color: #CCFFCC;
}
.sleder-controls__button {
  width: 80px;
  height: 30px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.sleder-controls__button-img {
  width: 100%;
  height: 100%;
}
.sleder-controls__button-img:hover {
  opacity: 0.7;
}
.sleder-controls__button-img:active {
  opacity: 0.3;
}
.sleder-controls__total {
  font-weight: bold;
  font-family: 'Lato', sans-serif;
}
.sleder-controls__total--current {
  font-size: 23px;
  align-self: start;
}
.sleder-controls__total--total {
  font-size: 18px;
  opacity: 0.7;
  align-self: end;
}
.slider-items {
  width: 500px;
}
@media (max-width: 600px) {
  .slider-items {
    width: 100%;
  }
}
.slider-items__item {
  text-align: center;
}
.slider-items__item--hidden {
  display: none;
}
.slider-items__img {
  width: 500px;
  height: 200px;
  object-fit: cover;
}
@media (max-width: 600px) {
  .slider-items__img {
    width: 100%;
  }
}
.sliderfull {
  position: relative;
  width: 100%;
  padding: 40px 20px;
  display: grid;
  grid-gap: 10px;
  justify-items: center;
  grid-template-columns: 1fr;
  grid-template-rows: min-content min-content;
  background-color: #F5F5F5;
}
@media (max-width: 600px) {
  .sliderfull {
    padding: 40px 0;
  }
}
.slederfull-controls {
  height: min-content;
  position: relative;
  width: min-content;
  display: grid;
  grid-template-columns: repeat(2, min-content);
  align-items: center;
  grid-column-gap: 20px;
}
.slederfull-controls::before {
  content: '';
  position: absolute;
  width: 4px;
  height: 100%;
  right: 50%;
  transform: translateX(100%) rotate(15deg);
  top: 0;
  background-color: #CCFFCC;
}
.slederfull-controls__total {
  font-weight: bold;
  font-family: 'Lato', sans-serif;
}
.slederfull-controls__total--current {
  font-size: 23px;
  align-self: start;
}
.slederfull-controls__total--total {
  font-size: 18px;
  opacity: 0.7;
  align-self: end;
}
.sliderfull-items__button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 40px;
  height: 50px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  z-index: 1;
}
.sliderfull-items__button--next {
  right: 10px;
}
.sliderfull-items__button--prev {
  left: 10px;
}
.sliderfull-items__button-img {
  width: 100%;
  height: 100%;
}
.sliderfull-items__button-img:hover {
  opacity: 0.7;
}
.sliderfull-items__button-img:active {
  opacity: 0.3;
}
.sliderfull-indicators {
  z-index: 1;
  max-width: 350px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  position: absolute;
  bottom: calc(40px + 20px);
  left: 50%;
  transform: translateX(-50%);
}
.sliderfull-indicators__item {
  list-style: none;
}
.sliderfull-indicators__button {
  margin: 0 3px;
  width: 40px;
  height: 10px;
  border: none;
  border-radius: 8px;
  background-color: #fff;
  opacity: 0.5;
  transition: 0.4s ease;
  cursor: pointer;
}
@media (max-width: 600px) {
  .sliderfull-indicators__button {
    width: 30px;
    height: 10px;
  }
}
.sliderfull-indicators__button--active {
  background-color: tomato;
  opacity: 1;
}
.sliderfull-indicators__button:hover,
.sliderfull-indicators__button:focus {
  opacity: 0.8;
}
.sliderfull-indicators__button:active {
  transition: 0s;
  opacity: 1;
}
.sliderfull-items {
  width: 500px;
  overflow: hidden;
  position: relative;
}
@media (max-width: 600px) {
  .sliderfull-items {
    width: 100%;
  }
}
.sliderfull-items__inner {
  display: flex;
  transition: 0.6s all;
}
.sliderfull-items__item {
  text-align: center;
}
.sliderfull-items__img {
  width: 500px;
  height: 200px;
  object-fit: cover;
}
@media (max-width: 600px) {
  .sliderfull-items__img {
    width: 100%;
  }
}
